import React from 'react';
import { Icon, getTheme, mergeStyleSets, normalize, initializeIcons, IconButton, DefaultButton } from '@fluentui/react';
import { Stack } from '@fluentui/react';
import { Spinner } from '@fluentui/react';
import { Label } from '@fluentui/react';
import { createChart } from 'lightweight-charts';
import { PrimaryButton } from '@fluentui/react';
import { List, ScrollToMode, IList } from '@fluentui/react/lib/List';
import { Callout, TextField, SearchBox } from '@fluentui/react';
import { Link, DatePicker, TimePicker } from '@fluentui/react';
import { Nav, INavLink, INavStyles, INavLinkGroup } from '@fluentui/react/lib/Nav';
const theme = getTheme();
initializeIcons();
const styles = mergeStyleSets({
    container: {
        overflow: 'auto',
        maxHeight: 400,
        border: '1px solid #CCC',
        selectors: {
            '.ms-List-cell .odd': {
                height: 40,
                lineHeight: 40,
            },
            '.ms-List-cell .even': {
                height: 40,
                lineHeight: 40,
                background: theme.palette.neutralLighter,
            },
        },
    },
    itemContent: [
        theme.fonts.medium,
        normalize,
        {
            position: 'relative',
            boxSizing: 'border-box',
            display: 'block',
            borderLeft: '3px solid ' + theme.palette.themePrimary,
            paddingLeft: 27,
        },
    ],
});
const url = 'http://localhost:3000'
const PairIcon = ({ base, quote }: any) => {
    return (<>
        <div style={{
            position: 'relative',
            width: '20px',
            height: '10px',
        }}>
            <img src={
                `${url}/icons/${quote}.svg`
            } style={{
                position: 'absolute',
                borderRadius: '50%',
                left: '8px',
                top: '0',
                overflow: 'hidden',
                width: '10px',
                height: '10px',
            }} />
            <img src={
                `${url}/icons/${base}.svg`
            } style={{
                position: 'absolute',
                left: '0',
                borderRadius: '50%',
                top: '0',
                overflow: 'hidden',
                width: '10px',
                height: '10px',
            }} />
        </div>
    </>)
}

const Chart = ({ trades, setTrades,
    gotoTrade, chartRef, chart, priceSeries, isLoading, setIsLoading, isLoadingMessage, setIsLoadingMessage }: any) => {

    return (<>
        {isLoading ?
            <div style={{
                height: '400px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }}>
                <Spinner label={isLoadingMessage} />
            </div>
            : null
        }

        <>
            <div ref={chartRef} style={{
                width: '100%',
                display: isLoading ? 'none' : '',
            }} />
        </>
    </>)
}
const Info = () => {
    const [isCalloutVisible, setIsCalloutVisible] = React.useState<boolean>(false);
    return (<>
        {isCalloutVisible && <Callout
            role="dialog"
            gapSpace={0}
            target={`#infobtn`}
            onDismiss={() => setIsCalloutVisible(false)}
            setInitialFocus
        >
            <Stack style={{
                padding: '10px',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
            }}>
                <Label>
                    Charts and Financial Instruments Icons by <Link href="https://www.tradingview.com/" target="_blank">TradingView</Link>
                </Label>
                <Label>
                    Performance data is not simulated last update was pushed to git on {new Date().toLocaleString()}
                </Label>
                <Label>
                    This is not an investment product, just hosting my reports on git 
                </Label>
            </Stack>
        </Callout>}

        <div style={{
            width: '200px',
            height: '5vh',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontWeight: 'bold',
            fontSize: '0.2em',
        }}>
            <Label style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }}>
                <DefaultButton style={{
                    fontWeight: 'bold',
                    fontSize: '0.8em',
                    marginRight: '10px',
                    marginLeft: '10px',
                }} id="infobtn" onClick={() => setIsCalloutVisible(!isCalloutVisible)} text={`Last Updated: ${new Date().toLocaleString()}`} iconProps={{ iconName: 'Info' }} />
            </Label>
        </div>
    </>)
}
function App() {
    const [trades, setTrades] = React.useState<any[]>([]);
    const [tradesFilter, setTradesFilter] = React.useState<any>('');
    const [filters, setFilters] = React.useState<any>({});
    const [search, setSearch] = React.useState<any>('');
    const [startDate, setStartDate] = React.useState<any>(new Date(new Date().getTime() - 1000 * 60 * 60 * 24 * 30));
    const [endDate, setEndDate] = React.useState<any>(new Date());
    const [isLoading, setIsLoading] = React.useState<boolean>(false);
    const [isLoadingMessage, setIsLoadingMessage] = React.useState<any>('');
    const [menu, setMenu] = React.useState<any>([]);
    React.useEffect(() => {
        fetch(`${url}/data/strategies.json`).then((res) => res.json()).then((data) => {
            setMenu([
                {
                    links: data.map((item: any) => {
                        return {
                            name: (<>
                                <span style={{
                                    fontWeight: 600,
                                    fontSize: '0.8rem',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                }}>
                                    {item.name} <img src={
                                        `${url}/icons/${item.marketIcon}.svg`
                                    } style={{
                                        width: '1rem',
                                        height: '1rem',
                                        marginLeft: '0.5rem',
                                    }} />
                                </span>
                            </>),
                            isExpanded: true,
                            key: item.name + '-' + item.market,
                            links: item.markets.map((market: any) => {
                                return {
                                    name: (<>
                                        <span style={{
                                            fontWeight: 800,
                                            fontSize: '10px',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                        }}>
                                            <PairIcon base={market.base} quote={market.quote} />   {market.name}
                                        </span>
                                    </>),
                                    url: `/#strategies/${item.name}/${market.name}`,
                                    icon: item.marketIcon,
                                    key: item.name + '-' + item.market + '-' + market.name,
                                }
                            })
                        }
                    })
                }]);
        });
    }, []);
    const chartRef = React.useRef<any>('');
    const chart = React.useRef<any>('');
    const priceSeries = React.useRef<any>('');
    const gotoTrade = React.useCallback((trade: any) => {
        chart.current.timeScale().setVisibleRange({
            from: new Date(new Date(trade.time).getTime() - 1000 * 60 * 60 * 24).getTime(),
            to: new Date(new Date(trade.time).getTime() + 1000 * 60 * 60 * 24).getTime(),
        });
    }, [chartRef, chart, priceSeries]);
    const searchedTrades = React.useCallback(() => {
        const filtered = trades.filter((item: any) => Object.keys(item).some((key: any) => item[key].toString().toLowerCase().includes(search.toLowerCase())))
            .filter((item: any) => new Date(item.time).getTime() >= startDate.getTime() && new Date(item.time).getTime() <= endDate.getTime())
        setTradesFilter(filtered);
    }, [trades, search, startDate, endDate]);
    React.useEffect(() => {
        searchedTrades();
    }, [trades, search, startDate, endDate]);
    React.useEffect(() => {
        if (!!chartRef.current) return () => {
            const priceFormat = (price: number) => {
                return `$${price.toFixed(5)}`;
            };
            const timeFormat = (time: any) => {
                return new Date(time).toLocaleString()
            };
            chart.current = createChart(chartRef.current,
                {
                    width: chartRef.current.offsetWidth,
                    height: 400,
                    layout: {

                    },
                    grid: {
                        vertLines: {
                            color: theme.palette.neutralLight,
                            style: 1,
                            visible: true,
                        },
                        horzLines: {
                            color: theme.palette.neutralLight,
                            style: 1,
                            visible: true,
                        },
                    },
                    rightPriceScale: {
                        borderVisible: false,
                        ticksVisible: true,
                        borderColor: theme.palette.neutralLight,
                        scaleMargins: {
                            top: 0.3,
                            bottom: 0.25,
                        },

                    },
                    timeScale: {
                        borderVisible: false,
                        tickMarkFormatter: (time: any) => {
                            const month = new Date(time).getMonth();
                            const year = new Date(time).getFullYear();
                            return new Date(time).toLocaleString('default', {
                                day: 'numeric',
                                month: 'short',
                                year: 'numeric'
                            });
                        },
                    },
                    localization: {
                        priceFormatter: priceFormat,
                        timeFormatter: timeFormat,
                    },
                }
            );
            fetchData().then((data: any) => {
                alert('done')
            });
        }
    }, [chartRef]);
    React.useEffect(() => {
        if (!!chartRef.current) {
            window.addEventListener('resize', () => {
                chart.current.resize(chartRef.current.offsetWidth, chartRef.current.offsetHeight);
            });
            return () => {
                window.removeEventListener('resize', () => {
                    chart.current.resize(chartRef.current.offsetWidth, chartRef.current.offsetHeight);
                });
            }
        }
    }, [chartRef]);
    const fetchData = React.useCallback(() => new Promise((resolve, reject) => {
        setIsLoadingMessage('Fetching Prices');
        setIsLoading(true);
        fetch(
            `${url}/data/mean_reversion/binance.json`
        ).then((response) => {
            return response.json();
        }).then((data) => {
            console.log(data)
            priceSeries.current = chart.current.addCandlestickSeries({
                upColor: theme.palette.green,
                downColor: theme.palette.red,
                borderDownColor: theme.palette.red,
                borderUpColor: theme.palette.green,
                wickDownColor: theme.palette.red,
                wickUpColor: theme.palette.green,
            });
            const priceData = data.price.map((item: any) => {
                return { open: item.open, high: item.high, low: item.low, close: item.close, time: new Date(item.time.close).getTime() }
            }).sort((a: any, b: any) => {
                return a.time - b.time;
            });
            priceSeries.current.setData(priceData);
            chart.current.timeScale().fitContent()
            const trades = data.trades
                .filter((item: any) => item.symbolId === '1')
                .map((item: any) => ({
                    time: new Date(parseInt(item.executionTimestamp)).getTime(),
                    position: item.tradeSide === 'BUY' ? 'belowBar' : 'aboveBar',
                    color: item.tradeSide === 'BUY' ? theme.palette.green : theme.palette.red,
                    shape: item.tradeSide === 'BUY' ? 'arrowUp' : 'arrowDown',
                    text: item.tradeSide === 'BUY' ? 'Buy @ ' + item.executionPrice : 'Sell @ ' + item.executionPrice,
                    price: item.executionPrice,
                    side: item.tradeSide,
                    _time: new Date(parseInt(item.executionTimestamp)).toLocaleString(),
                    gototrade: (<>
                        <PrimaryButton onClick={() => {
                            gotoTrade(item);
                        }} text="On Chart" />
                    </>),
                    size: 1,
                }) as any).sort((a: any, b: any) => {
                    return a.time - b.time;
                });
            priceSeries.current.setMarkers(trades);

            setTrades(trades);
            const minDate = new Date(Math.min(...trades.map((item: any) => item.time)));
            const maxDate = new Date(Math.max(...trades.map((item: any) => item.time)));
            setStartDate(minDate);
            setEndDate(maxDate);
            setIsLoading(false);
        }).then(() => {
            chart.current.priceScale('right').applyOptions({
                scaleMargins: {
                    top: 0.3,
                    bottom: 0.25,
                },
                scaleStyle: {
                    borderColor: theme.palette.neutralLight,
                },
                steps: 0.00001,
            });
            chart.current.applyOptions({
                width: chartRef.current.offsetWidth,
            })
            chart.current.timeScale().scrollToPosition(0);
            chart.current.timeScale().fitContent();
            setIsLoading(false);
            resolve(true)
        });
    }), [chartRef, chart, priceSeries, setTrades, setStartDate, setEndDate, setIsLoading, theme.palette.green, theme.palette.red]);
    return (
        <Stack horizontal horizontalAlign="space-between" verticalAlign="start">
            <div style={{
                width: '200px',
            }}>
                <Nav
                    onLinkClick={() => { }}
                    selectedKey='key'
                    styles={{
                        root: {
                            width: '200px',
                            height: '95vh',
                            boxSizing: 'border-box',
                            border: '1px solid #eee',
                            overflowY: 'auto',
                        }
                    }}
                    groups={menu}
                />
                <Info />
            </div>
            <Stack style={{ width: '100%' }}>
                <div style={{ boxShadow: theme.effects.elevation8 }}>
                    <Chart
                        gotoTrade={gotoTrade}
                        trades={trades} setTrades={setTrades}
                        isLoading={isLoading} setIsLoading={setIsLoading}
                        isLoadingMessage={isLoadingMessage} setIsLoadingMessage={setIsLoadingMessage}
                        chartRef={chartRef} chart={chart} priceSeries={priceSeries}
                    />
                </div>
                <div style={{
                    boxShadow: theme.effects.elevation8,
                    width: '100%',
                }}>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                    }}>
                        <Stack horizontal style={{
                            margin: '0.4em',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }} tokens={{ childrenGap: 10 }}>
                            <SearchBox
                                style={{ width: '100px', marginLeft: '0.3em' }}
                                placeholder="Search"
                                onChange={(event, newValue) => setSearch(newValue)}
                            />
                            <DatePicker
                                value={startDate}
                                style={{ width: '150px', marginTop: '0.25em' }}
                                placeholder='From date'
                                onSelectDate={(date) => setStartDate(date)}
                            />
                            <DatePicker
                                value={endDate}
                                style={{ width: '150px', marginTop: '0.25em' }}
                                placeholder='To date'
                                onSelectDate={(date) => setEndDate(date)}
                            />
                        </Stack>
                    </div>

                    <div style={{ fontSize: '0.8em', fontWeight: 'bold', display: 'flex', width: '100%', paddingLeft: '2em' }}>
                        <div style={{ height: '20px', display: 'flex', width: '100%', margin: '1em', alignItems: 'center', justifyContent: 'center' }}>
                            <div style={{ height: '10px', width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Side<IconButton style={{ marginLeft: '0.4em' }} iconProps={{ iconName: 'Sort' }} onClick={() => {
                                setFilters({ ...filters, side: filters.side === 'BUY' ? 'SELL' : 'BUY' })
                                setTrades(trades.sort((a: any, b: any) => {
                                    if (filters.side === 'BUY' && a.side === 'SELL' && b.side === 'BUY') return 1;
                                    else return -1;
                                }));
                                searchedTrades();
                            }} /></div>
                            <div style={{ height: '10px', width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Price<IconButton style={{ marginLeft: '0.4em' }} iconProps={{ iconName: 'Sort' }} onClick={() => {
                                setFilters({ ...filters, price: filters.price === 'asc' ? 'desc' : 'asc' })
                                setTrades(trades.sort((a: any, b: any) => {
                                    if (filters.price === 'asc' && a.price > b.price) return 1;
                                    else return -1;
                                }));
                                searchedTrades();
                            }} /></div>
                            <div style={{ height: '10px', width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>Time<IconButton style={{ marginLeft: '0.4em' }} iconProps={{ iconName: 'Sort' }} onClick={() => {
                                setFilters({ ...filters, time: filters.time === 'asc' ? 'desc' : 'asc' })
                                setTrades(trades.sort((a: any, b: any) => {
                                    if (filters.time === 'asc' && a.time > b.time) return 1;
                                    else return -1;
                                }));
                                searchedTrades();
                            }} /></div>
                            <div style={{ height: '10px', width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}></div>
                        </div>
                    </div>
                    {isLoading ? <div style={{ height: '400px', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                        <Spinner label={isLoadingMessage} />
                    </div> :
                        <div className={styles.container} data-is-scrollable>
                            <List
                                style={{
                                    height: '400px',
                                    overflow: 'auto',
                                }}
                                items={tradesFilter}
                                getPageHeight={() => 50}
                                onRenderCell={(item: any, index: any) => {
                                    return (
                                        <div data-is-focusable className={index % 2 === 0 ? 'even' : 'odd'}>
                                            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }} className={styles.itemContent}>
                                                <div style={{ display: 'flex', width: '100%' }}>
                                                    <div style={{ width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>{item.side}</div>
                                                    <div style={{ width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>{item.price}</div>
                                                    <div style={{ width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>{item._time}</div>
                                                    <div style={{ width: '25%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                                                        <PrimaryButton onClick={() => {
                                                            gotoTrade(item);
                                                        }} text="On Chart" />
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    );
                                }}
                            />
                        </div>}
                </div>
            </Stack>
        </Stack>
    );
}

export default App;
